<div class="row">
  {#if hasIntroText(row)}
    {#each row as post, i (post.slug)}
      {#if post.category === 'categoryIntroduction'}
        <IntroTile {post} />
      {:else}
        <Tile {post} width={introRowTileWidths[i - 1]} order={i} />
      {/if}
    {/each}
  {:else}
    {#each row as post, i (post.slug)}
      <Tile {post} width={tileWidths[i]} order={i} />
    {/each}
  {/if}
