<div class="satoshi-container" class:tiled bind:this={satoshiEl}>
  {#if inView}
    <img
      src={tiled ? urlFor(imageObject)
            .width(1400)
            .height(440)
            .quality(90)
            .auto('format')
            .url() : urlFor(imageObject)
            .height(1400)
            .width(1000)
            .quality(90)
            .auto('format')
            .url()}
      on:load={() => (loaded = true)}
      class="satoshi-image {imageHeight}
      {imageWidth}
      {imagePosition}"
      class:loaded
      alt="Satoshi Fujiwara" />
  {/if}

</div>
