<script>
  // # # # # # # # # # # # # #
  //
  //  Tile
  //
  // # # # # # # # # # # # # #

  // *** IMPORT
  import { onMount } from "svelte";
  import { fly } from "svelte/transition";
  import { Router, links } from "svelte-routing";
  import MediaQuery from "svelte-media-query";
  // _lodash
  import kebabCase from "lodash/kebabCase";
  import get from "lodash/get";
  import isEmpty from "lodash/isEmpty";

  // *** STORES
  import {
    isArabic,
    isEnglish,
    categoryList,
    languagePrefix
  } from "../stores.js";

  // *** GLOBALS
  import { urlFor } from "../sanity.js";

  // *** PROPS
  export let post = {};
  export let width = 20;
  export let order = 0;

  // *** DOM REFERENCES
  let tileEl = {};

  // ** VARIABLES
  let color = "";
  let loaded = false;
  let linkOutActive = false;
  const imgWidth = width >= 25 ? 600 : 400;
  let inView = false;

  // >>> RE-USE
  $: {
    if (post.category) {
      let matchingCategory = $categoryList.find(
        cat => cat.categorySlug === kebabCase(post.category)
      );
      color = matchingCategory ? matchingCategory.color : "rfgen-white";
    }
  }
  // <<< RE-USE

  const observer = new IntersectionObserver(
    entries => {
      entries.forEach(entry => {
        if (entry.intersectionRatio > 0) {
          inView = true;
          observer.disconnect();
        }
      });
    },
    { threshold: 0.05 }
  );

  if (!post.mainImage) loaded = true;

  onMount(async () => {
    observer.observe(tileEl);
  });
</script>

