  font-family: Arial, Helvetica, sans-serif; }

.tile {
  width: 33.33%;
  height: 220px;
  margin: 0;
  float: left;
  position: relative;
  opacity: 1;
  cursor: pointer;
  margin-bottom: 2px; }
  @media (max-width: 700px) {
    .tile {
      width: 100%;
      height: 300px;
      margin-bottom: 0px; } }

.tile-bar {
  position: absolute;
  top: 0;
  left: 0;
  height: 35px;
  width: 100%;
  font-size: 16px;
  line-height: 1.1em;
  z-index: 10;
  overflow: hidden; }
  @media (max-width: 700px) {
    .tile-bar {
      font-size: 22px;
      line-height: 1.1em; } }

.tile-title {
  height: 35px;
  padding: 5px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  transition: opacity 1s cubic-bezier(0.23, 1, 0.32, 1);
  opacity: 0; }
  .tile-title.loaded {
    opacity: 1; }

.tile-overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 5px;
  font-size: 16px;
  line-height: 1.1em;
  height: calc(220px - 35px);
  height: 220px;
  z-index: 11;
  pointer-events: none;
  opacity: 0; }
  .tile-overlay.active {
    opacity: 1;
    pointer-events: all; }
  @media (max-width: 700px) {
    .tile-overlay {
      font-size: 22px;
      line-height: 1.1em;
      height: 300px; } }

.tile-image {
  margin-top: 35px;
  height: calc(220px - 35px);
  width: 100%;
  transition: opacity 0.5s cubic-bezier(0.23, 1, 0.32, 1);
  opacity: 0; }
  @media (max-width: 700px) {
    .tile-image {
      height: calc(300px - 35px); } }
  .tile-image img {
    height: 100%;
    width: 100%;
    object-fit: cover; }
  .tile-image.loaded {
    opacity: 1; }

.external-link {
  text-decoration: underline; }
  .external-link:hover {
    text-decoration: none; }

.width-15 {
  width: 15%; }
  @media (max-width: 700px) {
    .width-15 {
      width: 100%; } }

.width-20 {
  width: 20%; }
  @media (max-width: 700px) {
    .width-20 {
      width: 100%; } }

.width-25 {
  width: 25%; }
  @media (max-width: 700px) {
    .width-25 {
      width: 100%; } }

.width-30 {
  width: 30%; }
  @media (max-width: 700px) {
    .width-30 {
      width: 100%; } }

.width-35 {
  width: 35%; }
  @media (max-width: 700px) {
    .width-35 {
      width: 100%; } }

.width-40 {
  width: 40%; }
  @media (max-width: 700px) {
    .width-40 {
      width: 100%; } }

.order-0,
.order-1,
.order-2,
.order-3 {
  border-right: 2px solid white; }
  @media (max-width: 700px) {
    .order-0,
    .order-1,
    .order-2,
    .order-3 {
      border-right: 0px solid white; } }</style>

<Router>

  <div
    class="tile width-{width} order-{order}
    {color}"
    use:links
    bind:this={tileEl}>

    {#if post.category === 'writing' && !post.isSticky}
      <div on:click={e => (linkOutActive = !linkOutActive)}>
        <div class="tile-bar">
          {#if !linkOutActive}
            <div class="tile-title" class:loaded>
              {#if $isEnglish}{post.en_title}{/if}
              {#if $isArabic}{post.ar_title}{/if}
            </div>
          {/if}
        </div>
        <div class="tile-image" class:loaded>
          {#if post.mainImage && inView}
            <MediaQuery query="(min-width: 800px)" let:matches>
              {#if matches}
                <img
                  src={urlFor(post.mainImage)
                    .height(320)
                    .width(imgWidth)
                    .quality(80)
                    .auto('format')
                    .url()}
                  on:load={() => (loaded = true)}
                  alt={$isEnglish ? post.en_title : post.ar_title} />
              {:else}
                <img
                  src={urlFor(post.mainImage)
                    .height(300)
                    .width(600)
                    .quality(80)
                    .auto('format')
                    .url()}
                  on:load={() => (loaded = true)}
                  alt={$isEnglish ? post.en_title : post.ar_title} />
              {/if}
            </MediaQuery>
          {/if}
        </div>
        <div class="tile-overlay {color}" class:active={linkOutActive}>
          {#if linkOutActive}
            {#if !isEmpty(post.participants)}
              <p in:fly={{ duration: 150, y: 10 }}>
                <a
                  href="/{$languagePrefix}/participant/{get(post, 'participants[0].slug', '')}"
                  class="author">
                  {get(post, 'participants[0].en_title', '')}
                </a>
              </p>
            {/if}
            <p in:fly={{ duration: 150, delay: 100, y: 10 }}>
              {#if $isEnglish}{post.en_title}{/if}
              {#if $isArabic}{post.ar_title}{/if}
            </p>

            <p in:fly={{ duration: 150, delay: 200, y: 10 }}>
              {#if $isEnglish}
                <a href={post.link} target="_blank" class="external-link">
                  Read on {post.publisherName}
                </a>
              {/if}
              {#if $isArabic && post.ar_linkFile}
                <a href="{post.ar_linkFile}?dl=" class="external-link" download>
                  {post.ar_linkText}
                </a>
              {/if}
            </p>
          {/if}
        </div>
      </div>
    {:else}
      <a href="/{$languagePrefix}/{post.category}/{post.slug}">
        <div class="tile-bar">
          {#if !linkOutActive}
            <div class="tile-title" class:loaded>
              {#if $isEnglish && post.en_title}{post.en_title}{/if}
              {#if $isArabic && post.ar_title}{post.ar_title}{/if}
            </div>
          {/if}
        </div>
        <div class="tile-image" class:loaded>
          {#if post.mainImage && inView}
            <MediaQuery query="(min-width: 800px)" let:matches>
              {#if matches}
                <img
                  src={urlFor(post.mainImage)
                    .height(320)
                    .width(imgWidth)
                    .quality(80)
                    .auto('format')
                    .url()}
                  on:load={() => (loaded = true)}
                  alt={$isEnglish ? post.en_title : post.ar_title} />
              {:else}
                <img
                  src={urlFor(post.mainImage)
                    .height(300)
                    .width(600)
                    .quality(80)
                    .auto('format')
                    .url()}
                  on:load={() => (loaded = true)}
                  alt={$isEnglish ? post.en_title : post.ar_title} />
              {/if}
            </MediaQuery>
          {/if}
        </div>
      </a>
    {/if}
  </div>
</Router>
