.rfgen-grey {
  color: black; }

.text-arabic {
  direction: rtl;
  font-family: Arial, Helvetica, sans-serif; }

.tile-view {
  width: 100vw;
  display: inline-block;
  line-height: 0;
  margin-top: 80px;
  padding-bottom: 50px;
  min-height: calc(100vh - 120px); }

.satoshi-strip {
  height: 440px; }
  @media (max-width: 700px) {
    .satoshi-strip {
      display: none; } }
  .satoshi-strip.tall {
    height: calc( 100vh - 80px - 50px); }</style>

<MetaData post={metaObject} />

<div class="tile-view">
  {#await posts}
    <div />
  {:then posts}
    {#each splitRows(posts) as row, i (uniqueId('row_'))}
      {#if row.satoshi}
        <div class="satoshi-strip {sample(colorList)}">
          <Satoshi tiled={true} satoshiIndex={(Math.round(i / 3) - 1) % 10} />
        </div>
      {:else}
        <Row {row} />
      {/if}
    {/each}
    {#if isEmpty($activeNavigation)}
      <div class="satoshi-strip {sample(colorList)} tall">
        <Satoshi satoshiIndex={5} />
      </div>
    {/if}
  {:catch error}
    <p style="color: red">{error.message}</p>
  {/await}
</div>
